.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.button {
  padding: 5px;
  color: whitesmoke;
  border-radius: 5px;
  background-color: rgba(38, 2, 44, 0.79);
  border: solid 0.5px whitesmoke;
}

.button:hover {
  cursor: pointer;
  box-shadow: 0px 0px 6px whitesmoke;
}
